@media screen and (min-width: 1024px) {
    .rightPart {
        width: calc(50% - 54px);
        position: absolute;
        top: 48px;
        right: 24px;
        bottom: 0;
        box-sizing: border-box;
        padding: 0 30px;
        overflow-y: auto;
        scrollbar-width: none;

    }

    .container_rightPart {
        overflow-y: auto;
    }

    .rightPart::-webkit-scrollbar {
        display: none;
    }
}